.MainMenu {
    display: flex;
    flex-direction: column;
    color: white;
    background: linear-gradient(175deg, #19857b 60%, #4F8EA0  90%);
    padding-left: 4px;
    -webkit-box-shadow: inset -10px -1px 10px -13px rgba(0,0,0,0.75);
    -moz-box-shadow: inset -10px -1px 10px -13px rgba(0,0,0,0.75);
    box-shadow: inset -10px -1px 10px -13px rgba(0,0,0,0.75);
}