@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&family=Montserrat&family=Playfair+Display:wght@500&display=swap);
body {
  margin: 0;
  font-family: Lato;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height:100%;
  overflow: hidden;
}

html {
  height:100%;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  height:100%;
}

.contentPane {
    color: #333;
    width:100%;
    height: inherit;
    overflow: hidden;
}
.content {
    overflow: hidden;
    height: 100%;
    width: inherit;
}
.MainMenuItem {
    font-size: 80%;
    display: inline-flex;
    flex-direction: column;
    width: 65px;
    height: 60px;
    min-height: 60px;
    padding: 0 4px 0 2px;
    justify-content: center;
    cursor: pointer;
    webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Old versions of Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
    margin: 1px 0;
    letter-spacing: 0.05em;
}
.MainMenuNotSelectedItem:hover {
    opacity: .25;
    background: aliceblue;
    color: black;
    border-radius: 7px 0 0 7px;
}

.MainMenuNotSelectedItem {
    opacity: .5;
    padding-right: 6px;
}

.MainMenuSelectedItem {
    opacity: 1;
    /*background: aliceblue;*/
    border-radius: 7px 0 0 7px;
    color: black;
    background: linear-gradient(45deg, #ef6c00 30%, #e65100 90%);
    /*border-right: 3px solid #e65100;*/
    animation-name: fadeInOpacity;
    animation-iteration-count: 1;
    animation-timing-function: ease-in;
    animation-duration: 0.3s;
}

.MainMenuItem DIV {
    justify-content: center;
    display: flex;
}

@keyframes fadeInOpacity {
    0% {
        opacity: .5;
    }
    100% {
        opacity: 1;
    }
}


.MainMenu {
    display: flex;
    flex-direction: column;
    color: white;
    background: linear-gradient(175deg, #19857b 60%, #4F8EA0  90%);
    padding-left: 4px;
    box-shadow: inset -10px -1px 10px -13px rgba(0,0,0,0.75);
}
.MainContent {
    display: flex;
    flex-direction: row;
    width:100%;
    height: 100%;
}
.SubContent {
    display: flex;
    flex-direction: column;
    width:100%;
    height: 100%;
    background: #e5e5e5;
}
.SubMenu {
    display: flex;
    flex-direction: column;
    color: #ddd;
    min-width: 180px;
    width: 200px;
    padding: 5px 12px 5px 5px;
    box-shadow: inset -10px -1px 10px -13px rgba(0,0,0,0.75);
    background: linear-gradient(45deg, lightslategray 30%, #666 90%);;
}

.SubMenuHeader {
    font-size: 150%;
    margin: 0 5px 15px 0;
    padding: 5px;
    border-bottom: 1px #bbb solid;
    font-weight: 500;
}

.SubMenuHeader SPAN {
    font-weight: 300;
}

.SubMenu .SubMenuFiller {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}

.SubMenu .SubMenuFiller .Branding {
    margin: 5px;
    border-top: 1px #bbb solid;
    padding: 10px 5px 5px 5px;
}
.SubMenu .SubMenuFiller .Branding .TagName {
    font-size: 80%;
    font-weight: 300;
    letter-spacing: 0.08em;
}
.SubMenu .SubMenuFiller .Branding .BrandName SPAN {
    font-weight: 300;
    font-size: 80%;
}
.SubMenu .SubMenuFiller .Branding .BrandName {
    font-size: 120%;
}
.SubMenuItem {
    margin: 1px 5px 1px 10px;
    color: #eee;
    padding: 5px 8px;
    cursor: pointer;
}

.SubMenuSelectedItem {
    background: linear-gradient(45deg, #ef6c00 30%, #e65100 90%);
    border-radius: 4px;
    color: #334;
    animation-name: transition;
    animation-iteration-count: 1;
    animation-duration: 1s;
}

.SubMenuNotSelectedItem:hover {
    background: #666;
    opacity: .75;
    border-radius: 4px;
    color: white;
}

@keyframes transition {
    0% {
        background: #666;
    }
    100% {
        background: #ef6c00;
    }
}

.ListDetailView {
    width: 100%;
    display: flex;
    height: 100%;
}

.ListDetailView .ListDetailViewLeftColumn {
    width: 45%;
    height: 100%;
    border-right: 1px solid #ccc;
    min-width: 300px;
}

.ListDetailView .ListDetailViewRightColumn {
    width: 55%;
    height: 100%;
    overflow-y: auto;
}
.FilterListWrapper {
    height: inherit;
    background: white;
}

.FilterInput {
    background: #f5f5f5;
    border-bottom: solid 1px #ddd;
    padding: 10px;
    min-height: 60px;
}

.FilterList {
    flex-wrap: wrap;
    border-left: solid 1px #ddd;
    border-right: solid 1px #ddd;
    height: calc(100% - 108px);
    overflow: auto;
}
.Card SPAN.hostname {
    font-weight: 300;
}

.Card .ScopeAvatar {

}
.Card .Avatar {
    border: 2px solid;
    font-size: .8em;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin:  10px;
    color: #555;
}

.Card {
    display: flex;
    align-items: center;
    width:100%;
    background: white;
    border-bottom: 1px solid #ddd;
    cursor: pointer;
    max-height: 50px;
    animation-name: cardVisible;
    animation-iteration-count: 1;
    animation-timing-function: ease-in;
    animation-duration: 0.5s;
}

@keyframes cardVisible {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.Card:hover {
    background: #EEE;
    color: black;
}

.Card .scope {
    margin: 0 5px;
    width: 100px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.Card SPAN.timestamp {
    font-size: 90%;
}
.Card SPAN.timestamp,
.Card SPAN.scope {
    font-weight: 300;
}

.Card SPAN.timestamp,
.Card SPAN.scope,
.Card SPAN.type {
    flex: 1 1;
    padding: 0 4px;
}

.Card SPAN.name {
    flex: 2 1;
    padding: 0 4px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.Description {
    color: #eee;
}
.TrackerCode {
    width: 100%;
    overflow-x: auto;
}
.DetailBox {
    background: #f5f5f5;
    padding: 5px 20px 20px 20px;
    margin: 8px 0;
    border-radius: 10px;
    color: rgb(0, 105, 192);
    box-shadow: 0px 0px 7px -4px rgba(0,0,0,0.75);
    animation-name: boxVisible;
    animation-iteration-count: 1;
    animation-timing-function: ease-out;
    animation-duration: 0.4s;
}

@keyframes boxVisible {
    0% {
        opacity: 0;
        transform: scale(.96);
    }
    100% {
        opacity: 1;
        transform: scale(1);
    }
}
.MiniHeader {
    text-transform: uppercase;
    font-size: 10px;
    width: 100%;
    color: black;
    padding: 8px 0;
    letter-spacing: 0.10em
}
.Head {
    display: flex;
    flex-direction: row;
    align-items: center;
}

DIV.Description {
    line-height: 3em;
    color: #282c34;
}

.Head H1 {
    margin: 0 0 0 10px;
    font-size: 160%;
    font-weight: 300;
    text-transform: uppercase;
}
.DetailHeader {
    display: flex;
    flex-direction: row;
    align-items: center;
    background: #f5f5f5;
    min-height: 61px;
    border-bottom: solid 1px #ddd;
    animation-name: detailHeaderVisible;
    animation-iteration-count: 1;
    animation-timing-function: ease-out;
    animation-duration: .5s;
}

.DetailHeader  .DetailLabel {
    text-transform: uppercase;
    font-size: 160%;
    margin: 13px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    letter-spacing: 0.08em;
}

@keyframes detailHeaderVisible {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.RightTabScroller {
    height: calc(100% - 109px);
    overflow-y: auto;
    padding: 5px 8px;
    overflow-x: hidden;
}
.DetailActionButtons {
    padding: 10px;
    display: flex;
    justify-items: right;
    flex-direction: row;
    border-top: solid #ccc 1px;
    background: #f5f5f5;
}
.DetailKeyValue {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 5px ;
    margin: -1px 0;
}

.DetailKeyValue .DetailValue {
    flex: 1 1;
    overflow: hidden;
    text-overflow: ellipsis;
    width: 100%;
    padding-left: 10px;
    border-bottom: solid 1px #ddd;
}

.DetailKeyValue .DetailKey {
    flex: .5 1;
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding-left: 10px;
    border-bottom: solid 1px #ddd;
}


.TopInfoBar {
    min-height: 46px;
    border-bottom: 1px #ccc solid;
    display: flex;
    flex-direction: row;
    align-content: center;
    margin: 0 5px 0px 0px;
    font-size: 90%;
    width: inherit;
    padding: 4px;
    background: white;
}

.TopInfoBar .LogOutButton {
    display: flex;
    width: 125px;
    justify-content: right;
}

.TopInfoBar .ActionButtons {
    display: flex;
    width: 100%;
    justify-content: right;
}

.TopInfoBar .Path {
    white-space: nowrap;
    display: flex;
    flex-direction: row;
    align-content: center;
    color: #666;
}

.Breadcrumps {
    display: flex;
    align-items: center;
    flex-direction: row;
}

.Breadcrumps SPAN {
    margin: 4px;
}
.CenteredCircularProgress {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}
.Card .EventAvatar {

}

.RulePane {
    margin-top: 20px;
    margin-left: 50px;
}

.RuleDescription {
    font-weight: 300;
}

.RuleRawData H1 {
    font-size: 140%;
    font-weight: 300;
    text-transform: uppercase;
}

.RuleSwitch {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 0 5px ;
    margin: -1px 0;
}
.RuleSwitch DIV.RuleSwitchLabel {
    padding-left: 10px;
}
.RuleSwitch .RuleSwitchDescription{
    line-height: 2em;
}
.RuleSwitch .RuleSwitch{
    line-height: 1em;
}

.SmallTitle {
    font-size: 12px;
}

.SmallTitle PRE {
    margin:0
}
.Card .RuleAvatarEnabled {

}

.Card .RuleAvatarDisabled {

}

.Card .SegmentAvatarEnabled {

}

.Card .SegmentAvatarDisabled {

}
.Card .ProfileAvatar {

}

.QueryResponseColumns {
    width:100%;
    display: flex;
    height: inherit;
    position: relative;
}

.QueryResponseColumns .QueryResponseLeftColumn {
    width: 50%;
    height: 100%;
    background: whitesmoke;
    border: solid 1px #ccc;
}

.QueryResponseColumns .QueryResponseLeftColumn .RequestInfo {
    padding: 5px;
    width: 100%;
    background: whitesmoke;
    border-bottom: solid 1px #ccc;
    display: flex;
    align-items: center;
    color: #666;
    height: 42px;
}

.QueryResponseColumns .QueryResponseLeftColumn .RequestInfo SPAN {
    margin-right: 10px;
}

.QueryResponseColumns .QueryResponseLeftColumn .RequestBody {
    padding: 10px;
    width: 100%;
    height: calc(100% - 170px);
    overflow-y: auto;
}

.QueryResponseColumns .QueryResponseLeftColumn .RequestStatus {
    padding: 3px;
    width: 100%;
    background: whitesmoke;
    border-top: solid 1px #ccc;
    color: #666;
    height: 26px;
}

.QueryResponseColumns .QueryResponseRightColumn .ResponseInfo {
    padding: 5px;
    width: 100%;
    background: whitesmoke;
    border-bottom: solid 1px #ccc;
    height: 42px;
    display: flex;
    align-items: center;
}

.QueryResponseColumns .QueryResponseRightColumn .ResponseBody {
    padding: 10px;
    width: 100%;
    height: calc(100% - 170px);
    overflow-y: auto;
}

.QueryResponseColumns .QueryResponseRightColumn .ResponseStatus {
    padding: 3px;
    width: 100%;
    background: whitesmoke;
    border-top: solid 1px #ccc;
    color: #666;
    height: 26px;
}

.QueryResponseColumns .QueryResponseRightColumn {
    width: 50%;
    height: 100%;
    overflow: hidden;
    background: whitesmoke;
    border: solid 1px #ccc;
}
.QueryResponseColumns .RunQuery {
    cursor: pointer;
    align-items: center;
    justify-content: center;
    position: absolute;
    width:50px;
    height: 50px;
    top: 50%;
    left: 50%;
    display: flex;
    text-align: center;
    transform: translate(-50%, -50%);

    background: linear-gradient(45deg, #d81b60 30%, #880e4f 90%);
    color: white;
    border-radius: 50%;
}
.QueryResponseColumns .DisableQuery {
    align-items: center;
    justify-content: center;
    position: absolute;
    width:50px;
    height: 50px;
    top: 50%;
    left: 50%;
    display: flex;
    text-align: center;
    transform: translate(-50%, -50%);

    background: linear-gradient(45deg, #666 30%, #333 90%);
    color: white;
    border-radius: 50%;
}


.MethodTag {
    padding: 4px;
    margin: 2px 6px 2px 2px;
    border-radius: 4px;
    max-width: 100px;
}
.GETTag {
    background: #1565c0;
    color: white;
}

.POSTTag {
    background: #FF8E53;
    color: white;
}

.DELETETag {
    background: #d81b60;
    color: white;
}

.PUTTag {
    background: #FF8E53;
    color: black;
}
.StatusCodeTag {
    padding: 4px;
    margin: 2px 6px 2px 2px;
    border-radius: 4px;
    max-width: 100px;
}

.StatusCode200, .StatusCode204 {
    background: #43a047;
    color: white;
}

.StatusCode500 {
    background: #d81b60;
    color: white;
}

.StatusCode404 {
    background: #d81b60;
    color: white;
}
.ManualPage {
    width: inherit;
    height: calc(100% - 46px);
    overflow: auto;
    background: white;
}
.ManualPage A {
    color: #19857b;
}

.ManualPage H1 {
    color: #19857b;
    border-bottom: 1px #19857b solid;
    margin: 0;
}

.ManualPage CODE {
    background: #eee;
    display: inline-flex;
    padding: 10px;
    margin-left: 10px;
    border-radius: 8px;
    border: solid 1px #ccc;
    color: rgb(0, 105, 192);
}
.CreateRuleForm {
    margin-top: 10px;
}
DIV.formRow {
    display: flex;
}

DIV.description {
    margin-bottom: 20px;
    color: #555;
}


.ScopeDesc {
    margin-bottom: 20px;
    color: #555;
}
.SocialSplashRoot {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.SocialSplash {
    display: flex;
    flex-direction: row;
    background: linear-gradient(45deg, #9c27b0 30%, #03a9f4  90%);
    height: inherit;
    width: 85%;
    height: 90%;
    border-radius: 20px;
    box-shadow: 0px 0px 17px -4px rgba(0,0,0,0.75);
    animation-name: splashVisible;
    animation-iteration-count: 1;
    animation-timing-function: ease-out;
    animation-duration: .8s;
}

@keyframes splashVisible {
    0% {
        opacity: 0;
        transform: scale(1.1);
    }
    100% {
        opacity: 1;
        transform: scale(1);
    }
}


.SocialSplash .Image {
    display: flex;
    justify-content: center;
    align-items: center;
    flex:1 1;
}

.SocialSplash .Image IMG {
    width:80%;
    height: 80%;
}

.SocialSplash .Content {
    flex:1 1;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 1.1vw;
    margin: 20px 40px 20px 20px;
}

.SocialSplash .Content .Message P {
    line-height: 2.5em;
    font-family: "Lato";

}

.SocialSplash .Content .Message P:first-child {
    line-height: 2em;
    margin-bottom: 40px;
}


.SocialSplash .Content .Title {
    font-size: 3.3vw;
    text-align: center;
    font-family: "Playfair Display";

}
.SocialSplash .Content .Title .Brand {

    text-transform: uppercase;
}
.SocialSplash .Content .Tag {
    letter-spacing: 0.05em;
    margin-bottom: 2vw;
    font-size: 1.3vw;
    text-align: center;
    font-family: "Playfair Display";
}

.SocialSplash .Content .Continue {
    text-align: right;
    margin-top: 1vw;
    margin-right: 40px;
}
.SocialButton {
    display: inline-flex;
    align-content: center;
    padding: 6px 6px;
    border-radius: 5px;
    vertical-align: middle;
    margin-left: 5px;
    margin-right: 5px;
    box-shadow: 0px 0px 4px 0px rgba(0,0,0,0.5);
    background: #9c27b0;
    cursor: pointer;
    line-height: 1.5em;
    color: white;
    text-decoration: none;
}

.SocialButton:hover {
    opacity: .75;
}

.SocialButton SPAN.Label {
    margin-left: 5px;
}

.SocialButton SPAN.Icon {
    display: flex;
    align-items: center;
}
.RightPaperHeader {
    border-bottom: 1px #ccc solid;
    display: flex;
    align-items: center;
    min-height: 40px;
    margin: 5px;
    color: #555;
}

.RightPaperHeader .Label {
    text-transform: uppercase;
    font-size: 110%;

    margin-left: 20px;
    margin-right: 10px;
    display: inherit;
}
