.ManualPage {
    width: inherit;
    height: calc(100% - 46px);
    overflow: auto;
    background: white;
}
.ManualPage A {
    color: #19857b;
}

.ManualPage H1 {
    color: #19857b;
    border-bottom: 1px #19857b solid;
    margin: 0;
}

.ManualPage CODE {
    background: #eee;
    display: inline-flex;
    padding: 10px;
    margin-left: 10px;
    border-radius: 8px;
    border: solid 1px #ccc;
    color: rgb(0, 105, 192);
}